.locationList {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(12em, 1fr) );
    position: absolute;
    inset: auto 0 0 0;
    background: #000a;
    border-top: 1px solid #fffa;
    box-shadow: 0 0 1em #0008, inset 0 1em 1em -1em #fff8;
    backdrop-filter: blur(2px);
}
